import React, { useState, useEffect } from 'react';
import { X, Shield, User, Trash2, Plus, CheckCircle } from 'lucide-react';
import { useAuth } from '../context/AuthContext';
import { adminAPI, usersAPI } from '../services/api';
import './AdminPanel.css';

const AdminPanel = ({ onClose }) => {
  const { user } = useAuth();
  const [activeTab, setActiveTab] = useState('admins'); // 'admins' or 'users'
  const [admins, setAdmins] = useState([]);
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const isDeveloper = user?.name === 'Developer';

  // Fetch admins and users
  useEffect(() => {
    fetchData();
  }, [activeTab]);

  const fetchData = async () => {
    try {
      setLoading(true);
      setError(null);

      if (activeTab === 'admins') {
        const adminsList = await adminAPI.getAdmins();
        setAdmins(adminsList);
      } else {
        const usersList = await usersAPI.getAllUsers();
        setUsers(usersList);
      }
    } catch (err) {
      console.error('Error fetching data:', err);
      setError('   ');
    } finally {
      setLoading(false);
    }
  };

  const handleMakeAdmin = async (userId) => {
    if (!isDeveloper) {
      alert(' Developer   ');
      return;
    }

    try {
      // Call API to make user admin
      await adminAPI.makeAdmin(userId);
      alert('  ');
      setUsers(users.map(u => u.id === userId ? { ...u, is_admin: true } : u));
      fetchData();
    } catch (err) {
      console.error('Error making admin:', err);
      alert('   ');
    }
  };

  const handleRemoveAdmin = async (userId) => {
    if (!isDeveloper) {
      alert(' Developer   ');
      return;
    }

    if (window.confirm(' ,    ?')) {
      try {
        await adminAPI.removeAdmin(userId);
        alert(' ');
        setAdmins(admins.filter(a => a.id !== userId));
        fetchData();
      } catch (err) {
        console.error('Error removing admin:', err);
        alert('   ');
      }
    }
  };

  const handleBanUser = async (userId) => {
    if (!isDeveloper) {
      alert(' Developer   ');
      return;
    }

    const reason = prompt(' :');
    if (reason) {
      try {
        await adminAPI.banUser(userId, reason);
        alert(' ');
        fetchData();
      } catch (err) {
        console.error('Error banning user:', err);
        alert('   ');
      }
    }
  };

  const handleUnbanUser = async (userId) => {
    if (!isDeveloper) {
      alert(' Developer   ');
      return;
    }

    try {
      await adminAPI.unbanUser(userId);
      alert(' ');
      fetchData();
    } catch (err) {
      console.error('Error unbanning user:', err);
      alert('   ');
    }
  };

  return (
    <div className="admin-panel-overlay" onClick={onClose}>
      <div className="admin-panel-dialog" onClick={(e) => e.stopPropagation()}>
        <div className="admin-panel-header">
          <h2> </h2>
          <button className="admin-panel-close" onClick={onClose} aria-label="Close">
            <X size={20} />
          </button>
        </div>

        <div className="admin-panel-tabs">
          <button
            className={`admin-tab ${activeTab === 'admins' ? 'active' : ''}`}
            onClick={() => setActiveTab('admins')}
          >
            <Shield size={18} />
            
          </button>
          <button
            className={`admin-tab ${activeTab === 'users' ? 'active' : ''}`}
            onClick={() => setActiveTab('users')}
          >
            <User size={18} />
            
          </button>
        </div>

        <div className="admin-panel-content">
          {!isDeveloper && (
            <div className="access-denied">
              <Shield size={32} />
              <p>     Developer</p>
            </div>
          )}

          {isDeveloper && loading && (
            <p className="loading-text">...</p>
          )}

          {isDeveloper && error && (
            <p className="error-text">{error}</p>
          )}

          {isDeveloper && !loading && activeTab === 'admins' && (
            <div className="admins-list">
              {admins.length === 0 ? (
                <p className="empty-text"> </p>
              ) : (
                admins.map(admin => (
                  <div key={admin.id} className="admin-item">
                    <div className="admin-info">
                      <div className="admin-avatar">
                        {admin.avatar?.startsWith('data:') ? (
                          <img src={admin.avatar} alt={admin.name} />
                        ) : (
                          <Shield size={24} />
                        )}
                      </div>
                      <div className="admin-details">
                        <h4>{admin.name}</h4>
                        <p>{admin.email}</p>
                        <span className="admin-badge">
                          <CheckCircle size={14} />
                          
                        </span>
                      </div>
                    </div>
                    <button
                      className="btn-remove-admin"
                      onClick={() => handleRemoveAdmin(admin.id)}
                      title=" "
                    >
                      <Trash2 size={18} />
                    </button>
                  </div>
                ))
              )}
            </div>
          )}

          {isDeveloper && !loading && activeTab === 'users' && (
            <div className="users-list">
              {users.length === 0 ? (
                <p className="empty-text"> </p>
              ) : (
                users.map(usr => (
                  <div key={usr.id} className="user-item">
                    <div className="user-info">
                      <div className="user-avatar">
                        {usr.avatar?.startsWith('data:') ? (
                          <img src={usr.avatar} alt={usr.name} />
                        ) : (
                          <User size={24} />
                        )}
                      </div>
                      <div className="user-details">
                        <h4>{usr.name}</h4>
                        <p>{usr.email}</p>
                        <span className={`role-badge ${usr.role?.toLowerCase()}`}>
                          {usr.role || 'User'}
                        </span>
                        {usr.is_admin && (
                          <span className="admin-badge">Admin</span>
                        )}
                        {usr.is_banned && (
                          <span className="banned-badge"></span>
                        )}
                      </div>
                    </div>
                    <div className="user-actions">
                      {!usr.is_admin && (
                        <button
                          className="btn-make-admin"
                          onClick={() => handleMakeAdmin(usr.id)}
                          title=" "
                        >
                          <Plus size={18} />
                        </button>
                      )}
                      {usr.is_banned ? (
                        <button
                          className="btn-unban"
                          onClick={() => handleUnbanUser(usr.id)}
                          title=""
                        >
                          
                        </button>
                      ) : (
                        <button
                          className="btn-ban"
                          onClick={() => handleBanUser(usr.id)}
                          title=""
                        >
                          <Trash2 size={18} />
                        </button>
                      )}
                    </div>
                  </div>
                ))
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default AdminPanel;
