import React, { useState, useEffect } from 'react';
import { useAuth } from '../../context/AuthContext';
import { useNavigate } from 'react-router-dom';
import './Dashboard.css';
import { Package, TrendingUp, Clock, AlertCircle } from 'lucide-react';
import { projectsAPI, donationsAPI } from '../../services/api';

const Dashboard = () => {
  const { user, addNotification } = useAuth();
  const navigate = useNavigate();
  const [donorForm, setDonorForm] = useState({
    productName: '',
    quantity: '',
    expiryDate: '',
    description: ''
  });
  const [projects, setProjects] = useState([]);
  const [availableProducts, setAvailableProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Prevent rendering if user data is not ready
  if (!user) {
    return (
      <div style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: '100vh',
        backgroundColor: '#0b0c0d',
        color: '#e6e7eb'
      }}>
        <p>Loading dashboard...</p>
      </div>
    );
  }

  // Fetch projects on component mount
  useEffect(() => {
    const fetchProjects = async () => {
      try {
        setLoading(true);
        const data = await projectsAPI.getAll();
        setProjects(data);
        
        // Convert projects to available products for display
        const products = data.map((project, index) => ({
          id: project.id,
          name: project.name,
          icon: project.icon || '',
          quantity: `${Math.floor(Math.random() * 20) + 5} `,
          distance: `${Math.round(Math.random() * 5) + 1} `,
          description: project.description || ' ',
          status: '',
          goal_amount: project.goal_amount,
          current_amount: project.current_amount,
          is_verified: project.is_verified
        }));
        setAvailableProducts(products);
        setError(null);
      } catch (err) {
        console.error('Error fetching projects:', err);
        setError('Failed to load projects');
      } finally {
        setLoading(false);
      }
    };

    fetchProjects();
  }, []);

  // Normalize user role for rendering
  const userRole = user.role ? String(user.role).toLowerCase() : 'recipient';

  const handleDonorChange = (e) => {
    const { name, value } = e.target;
    setDonorForm(prev => ({ ...prev, [name]: value }));
  };

  const handleDonorSubmit = (e) => {
    e.preventDefault();
    addNotification(` "${donorForm.productName}"   `);
    setDonorForm({ productName: '', quantity: '', expiryDate: '', description: '' });
  };

  const handleTakeProduct = (productName) => {
    addNotification(` : ${productName}`);
  };

  const renderDonorDashboard = () => (
    <div className="dashboard-content">
      <div className="error-banner">
        <AlertCircle size={18} />
        <span>:    !</span>
      </div>

      <h2> </h2>
      <form className="donor-form" onSubmit={handleDonorSubmit}>
        <div className="form-group">
          <label> </label>
          <input
            type="text"
            name="productName"
            value={donorForm.productName}
            onChange={handleDonorChange}
            placeholder=", , ..."
            required
          />
        </div>
        <div className="form-group">
          <label></label>
          <input
            type="text"
            name="quantity"
            value={donorForm.quantity}
            onChange={handleDonorChange}
            placeholder="10 , 2 ..."
            required
          />
        </div>
        <div className="form-group">
          <label> </label>
          <input
            type="date"
            name="expiryDate"
            value={donorForm.expiryDate}
            onChange={handleDonorChange}
            required
          />
        </div>
        <div className="form-group">
          <label></label>
          <textarea
            name="description"
            value={donorForm.description}
            onChange={handleDonorChange}
            placeholder=" ..."
            rows="4"
          />
        </div>
        <button type="submit" className="btn-submit"> </button>
      </form>

      <div className="projects-section">
        <h3> </h3>
        {loading ? (
          <p style={{ color: '#8a8f98', textAlign: 'center', padding: '2rem' }}> ...</p>
        ) : error ? (
          <p style={{ color: '#ef4444', textAlign: 'center', padding: '2rem' }}>{error}</p>
        ) : projects.length === 0 ? (
          <p style={{ color: '#8a8f98', textAlign: 'center', padding: '2rem' }}> , </p>
        ) : (
          <div className="products-grid">
            {projects.map((project) => (
              <div key={project.id} className="product-card">
                <div className="product-icon">{project.icon || ''}</div>
                <h4>{project.name}</h4>
                <p>{project.description || ' '}</p>
                <div className="progress-container">
                  <div className="progress-bar">
                    <div 
                      className="progress-fill" 
                      style={{width: `${(project.current_amount / project.goal_amount) * 100}%`}}
                    ></div>
                  </div>
                  <p className="progress-text">${project.current_amount.toFixed(0)}/${project.goal_amount.toFixed(0)}</p>
                </div>
                <span className="verified-badge">{project.is_verified ? ' ' : ' '}</span>
              </div>
            ))}
          </div>
        )}
      </div>

      <div className="available-products">
        <h3> </h3>
        {loading ? (
          <p style={{ color: '#8a8f98', textAlign: 'center', padding: '2rem' }}> ...</p>
        ) : availableProducts.length === 0 ? (
          <p style={{ color: '#8a8f98', textAlign: 'center', padding: '2rem' }}> , </p>
        ) : (
          <div className="products-grid">
            {availableProducts.map((product) => (
              <div key={product.id} className="product-card">
                <div className="product-icon">{product.icon}</div>
                <h4>{product.name}</h4>
                <p className="product-qty">{product.quantity}</p>
                <p className="product-location"> {product.distance}</p>
                <p className="product-status">{product.status}</p>
              </div>
            ))}
          </div>
        )}
      </div>

      <div className="donation-history">
        <h3> </h3>
        <div className="donations-grid">
          <div className="donation-card">
            <div className="donation-icon">BREAD</div>
            <h4> </h4>
            <p>10 </p>
            <p className="status"></p>
            <p className="date">2  </p>
          </div>
          <div className="donation-card">
            <div className="donation-icon">MILK</div>
            <h4> </h4>
            <p>5 </p>
            <p className="status"></p>
            <p className="date">1  </p>
          </div>
          <div className="donation-card">
            <div className="donation-icon">SALAD</div>
            <h4> </h4>
            <p>3 </p>
            <p className="status"></p>
            <p className="date">2  </p>
          </div>
          <div className="donation-card">
            <div className="donation-icon">APPLE</div>
            <h4></h4>
            <p>15 </p>
            <p className="status"></p>
            <p className="date">3  </p>
          </div>
        </div>
      </div>
    </div>
  );

  const renderDelivererDashboard = () => (
    <div className="dashboard-content">
      <div className="error-banner">
        <AlertCircle size={18} />
        <span>:     !</span>
      </div>

      <h2> </h2>
      <div className="stats-grid">
        <div className="stat-card">
          <TrendingUp size={32} />
          <h3> </h3>
          <p>{user?.deliveries || 150}</p>
        </div>
        <div className="stat-card">
          <Package size={32} />
          <h3></h3>
          <p>{user?.rating || 4.8}</p>
        </div>
        <div className="stat-card">
          <Clock size={32} />
          <h3> </h3>
          <p>2 </p>
        </div>
      </div>

      <div className="orders-section">
        <h3> </h3>
        <div className="orders-grid">
          <div className="order-card">
            <div className="order-status"> </div>
            <h4>  1</h4>
            <p> , 45</p>
            <p> 14:00 - 16:00</p>
            <button className="btn-accept"></button>
          </div>
          <div className="order-card">
            <div className="order-status"> </div>
            <h4>  2</h4>
            <p>. , 12</p>
            <p> 10:00 - 12:00</p>
            <button className="btn-accept"></button>
          </div>
          <div className="order-card">
            <div className="order-status"> </div>
            <h4>  3</h4>
            <p>. , 88</p>
            <p>  15:00 - 17:00</p>
            <button className="btn-accept"></button>
          </div>
        </div>
      </div>
    </div>
  );

  const renderReceiverDashboard = () => (
    <div className="dashboard-content">
      <div className="error-banner">
        <AlertCircle size={18} />
        <span>    .   ""     .</span>
      </div>

      <h2> </h2>
      <div className="receiver-info">
        <p>    .   ""     .</p>
      </div>

      <div className="available-products">
        <h3> </h3>
        {loading ? (
          <p style={{ color: '#8a8f98', textAlign: 'center', padding: '2rem' }}> ...</p>
        ) : error ? (
          <p style={{ color: '#ef4444', textAlign: 'center', padding: '2rem' }}>{error}</p>
        ) : projects.length === 0 ? (
          <p style={{ color: '#8a8f98', textAlign: 'center', padding: '2rem' }}> , </p>
        ) : (
          <div className="products-grid">
            {projects.map((project) => (
              <button 
                key={project.id}
                className="product-card" 
                onClick={() => {
                  handleTakeProduct(project.name);
                  navigate('/map');
                }}
              >
                <div className="product-icon">{project.icon || ''}</div>
                <h4>{project.name}</h4>
                <p>{project.description || ' '}</p>
                <div className="progress-container">
                  <div className="progress-bar">
                    <div 
                      className="progress-fill" 
                      style={{width: `${(project.current_amount / project.goal_amount) * 100}%`}}
                    ></div>
                  </div>
                  <p className="progress-text">${project.current_amount.toFixed(0)}/${project.goal_amount.toFixed(0)}</p>
                </div>
                {project.latitude && project.longitude && (
                  <p className="location"> {Math.round(Math.random() * 5 + 1)} </p>
                )}
                <span className="product-action">  </span>
              </button>
            ))}
          </div>
        )}
      </div>
    </div>
  );

  return (
    <div className="dashboard-container">
      {userRole === 'donor' && renderDonorDashboard()}
      {userRole === 'courier' && renderDelivererDashboard()}
      {(userRole === 'recipient' || !['donor', 'courier'].includes(userRole)) && renderReceiverDashboard()}
    </div>
  );
};

export default Dashboard;